* {
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  background-color: #282c34;
  color: white;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

///////////////////////////////////////////

nav {
  position: fixed;
  width: 100%;
  height: 50px;
  z-index: 500;
  background-color: rgba($color: #000000, $alpha: 0.5);

  a {
    color: #61dafb;
    text-decoration: none;
  }
}

.portfolio-page {
  padding-top: 70px;
  height: 100%;
  width: 100%;
  background-image: none;

  &.home-page {
    background-image: url("assets/fed-of-circles-and-solitaries.svg");
  }
  &.music-page {
    background-image: url("assets/macdonald-2.svg");
  }
  &.video-page {
    background-image: url("assets/macdonald-of-clanranald-3.svg");
  }
  &.drawing-page {
    background-image: url("assets/macdonald-of-prince-edward-island.svg");
  }

  h1 {
    font-size: calc(18px + 3vmin);
  }
}

.portfolio-section {
  min-height: 400px;
  width: 80%;
  max-width: 800px;
  margin: 30px auto;
  padding: 15px;
  border-radius: 8px;

  background-color: rgb(#6d6f77, 0.8);
  box-shadow: 
  inset rgba(184, 178, 178) 0px 0px 1px,
  inset rgba(184, 178, 178) 0px 0px 5px,
  inset rgba(184, 178, 178) 0px 0px 15px;

  display: flex;
  flex-direction: column;
  justify-content: space-between;

  h2 {
    font-size: calc(16px + 2vmin);
  }
}
///////////////////////////////////////////


